.pageWrapper{
    padding-top: calc(68px + 2.5em);
    padding-bottom: 2.5em;
    
}
.text_content_overlay{
    background-color: white;
    border-radius: 1em;
    padding: 2em ;
}

/* accordians */
.est_info_accordion .card{
    border-left: none;
    border-right: none;
}
.est_info_accordion .card-header{
    background-color: white;
}

.est_info_accordion .accordion_chevron {
    float: right;
    text-align: center;
    right: 0;
    width: 24px;
    height: 24px;

  }
.accordion_chevron svg {
    height: auto;
    width: auto;
}

/* whathappens specific hopskotch,  expand if you reuse the pattern */

.what_happens .row .col-lg-4{
    margin: auto;
}
.what_happens .hopskotchImage{
    margin-bottom: 1em;
    min-height: unset;
    min-height: 250px;
}
.what_happens p{
    font-size:unset;
}


