/* IMAGES */
/* MUST BE PNG */

#calling_img{
    background-image: linear-gradient(
        rgba(255,255,255,0),
        rgba(255, 255, 255, .7)
      ),
        url("./../calling_est.png");
    background-size: cover;
    background-attachment: fixed;
    background-repeat: no-repeat;
    background-position: center center;
    height:100vh;
    max-width: 100%;  
    margin-bottom: -5em;
}

